import React, { useEffect, useState } from 'react'
import './Aunt.css'
import $ from 'jquery'
import 'jquery.cookie'
import classNames from 'classnames'

export default function Auntification() {
  const [login, setLogin] = useState('')
  const [pass, setPass] = useState('')
  const [openalert, setIsErr] = useState(false)

  const [errLogin, setErrLogin] = useState('')
  const [errPass, setErrPass] = useState('')
  const [err, setErr] = useState('')
  useEffect(()=>{err != '' && setIsErr(true)}, [err])
  useEffect(() => {
    if (openalert){
      setTimeout(() => setIsErr(false), 2000)
      setTimeout(() => setErr(''), 3000)
    }
  }, [openalert])

  function Auntification(e) {
    e.preventDefault()
    if (login != '' && pass != '') {
      $.post('/auntification', {login, pass}, data => {
        if (data) {
          data.login && setErrLogin(data.login)
          data.pass && setErr(data.pass)
          data.err && setErr(data.err)
          
        }
        else {
          $.getJSON('getsettings', (data) => {
            
            document.cookie = `logining=true; max-age=${data.timeOpen/1000};`
          })
        }
      }).fail(() => {
        setErr('Сервер не отвечает. Попробуйте позже')
      })
    } else {
      if (login == '') {
        setErr('Введите пожалуйста логин!')
      } else {
        setErr('Введите пожалуйста пароль!')
      }
    }
  }

  
  function handleLogin(e) {
    setLogin(e.target.value)
  }
  function handlePass(e) {
    setPass(e.target.value)
  }

  return (
    <div className='conAunti'>
    <form onSubmit={Auntification} className='Aunti'>
      <h1>Auntification</h1>
      <label>
      <input type="text" placeholder="Логин" value={login} onChange={handleLogin} onFocus={() => setErrLogin('')}/>
      <p className={classNames('inputErr', {openErr: errLogin != ''})}>{errLogin}</p>
      </label>
      <label>
      <input type="password" placeholder="Пароль" value={pass} onChange={handlePass} onFocus={() => setErrPass('')}/>
      <p className={classNames('inputErr', {openErr: errPass != ''})}>{errPass}</p>
      </label>
      <button>Войти</button>
      <p className={classNames('alert', {openalert})}>{err}</p>
    </form>
    </div>
  )
}
