import React, { useEffect, useState } from "react";
import './Panel.css'
import $, { data } from "jquery";

export default function PanelUsers() {
    const [login, setLogin] = useState("");
    const [pass, setPassword] = useState("");
    const [users, setUsers] = useState([]);
    const [role, setRole] = useState("user")
    function getTime(time) {
      if (time < 1000) {return {v: time, t: 'ms'}}
      else if (time < 60000) {return {v: time/1000, t: 's'}}
      else if (time < 3600000) {return {v: time/60000, t: 'm'}}
      else if (time < 86400000) {return {v: time/3600000, t: 'h'}}
      else if (time < 172800000) {return {v: time/86400000, t: 'd'}}
    }
    const [timeOpen, setTimeOpen] = useState(null)
    const [trypass, setTrypass] = useState(null)
    const [timeblock, setTimblock] = useState(null)
    const [timeOpenEdit, setTimeOpenEdit] = useState(null)
    useEffect(() => {
      $.getJSON('getsettings', (data) => {
        setTimeOpen(data.timeOpen/1000)
        setTimeOpenEdit(getTime(data.timeOpen))
        setTrypass(data.trypass)
        setTimblock(getTime(data.timeblock))
      })
    }, [])
    document.addEventListener('mousemove', (e) => setMove(true))
    const [isMove, setMove] = useState(false)
    useEffect(()=>{
      if (isMove && timeOpen) document.cookie = `logining=true; max-age=${timeOpen};`
      setMove(false)
    }, [isMove])
    
  
    useEffect(() => {$.getJSON('/users', (data) => setUsers(data))}, [users])
  
    const onChangeLogin = (e) => {
      setLogin(e.target.value);
    }
  
    const onGenerate = () => {
  
      login !== "" && $.post('/api/generate', {login}, (data) => {
        setPassword(data.pass);
      })
    }
  
    const onCreate = () => {
      $.post('/api/create', {login, pass, role}, () => {
        setLogin("")
        setPassword("")
        setIsOpenCreate(false);
      })
    }


    const [isOpenCreate, setIsOpenCreate] = useState(false)
    const [isOpenSettings, setIsOpenSettings] = useState(false)

  
    return (
      <div className="App">
      <div className="title">
        <button onClick={() => setIsOpenCreate(true)}>Создать пользователя</button>
        <button onClick={() => setIsOpenSettings(true)}>Настройки безопасности</button>
        <button onClick={() => {
          $.post('/exitinfo')
          $.removeCookie('logining')
        }}>Выход из системы</button>
      </div>
      {isOpenCreate && <div className="formCon" >
        <div className="conClose" onClick={() => setIsOpenCreate(false)}></div>
        <div className="formCreate" >
          <div className="con_create">
            <input type="text" onChange={onChangeLogin} placeholder="Логин" value={login}/>
            <button onClick={onGenerate}>Сгенерировать</button>
          </div>
          <div className="infoCon">
            {pass && 
            <div className="info create">
            <p>Логин: </p><p><b>{login}</b></p>
            <p>Пароль: </p><p><b>{pass}</b></p>
            <p>Роль:</p>
            <select value={role} onChange={e => setRole(e.target.value)}>
              <option value="user">Пользователь</option>
              <option value="admin">Администратор</option>
            </select>
            <button onClick={onCreate}>Создать</button>
            </div>}
          </div>
  
        </div>
      </div>}
      {isOpenSettings && <div className="formCon" >
        <div className="conClose" onClick={() => setIsOpenSettings(false)}></div>
        <div className="formCreate" >
          <div className="info setting">
            <p>Число попыток ввода пароля</p>
            <div className="inputNumber">
              <button onClick={() => setTrypass(trypass-1)}>-</button>
              <button disabled>{trypass}</button>
              <button onClick={() => setTrypass(trypass+1)}>+</button>
            </div>
            <p>Время блокировки</p>
            <div className="inputNumber">
            <input type="text" value={timeblock.v != ('s' ||'ms') && timeblock.v} onChange={(e) => setTimblock({v: Number(e.target.value), t: timeblock.t})}/>
            <select value={timeblock.t != 's' && timeblock.t !='ms' && timeblock.t} onChange={(e) => setTimblock({v: timeblock.v, t: e.target.value})}>
              <option value='m'>минут</option>
              <option value='h'>часов</option>
              <option value='d'>дней</option>
            </select>
            </div>
            <p>Время простоя приложения</p>
            <div className="inputNumber">
            <input type="text" value={timeOpenEdit.v != ('s' ||'ms') && timeOpenEdit.v} onChange={(e) => setTimeOpenEdit({v: e.target.value, t: timeOpenEdit.t})}/>
            <select value={timeOpenEdit.t != ('s' ||'ms') && timeOpenEdit.t} onChange={(e) => setTimeOpenEdit({v: timeOpenEdit.v, t: e.target.value})}>
              <option value='m'>минут</option>
              <option value='h'>часов</option>
              <option value='d'>дней</option>
            </select>
            </div>
            <button onClick={() =>{
              $.post('/setsettings', {trypass, timeblock, timeOpen:timeOpenEdit}, data=>{
                setTimeOpen(data.timeOpen)
                setIsOpenSettings(false)
              })
            }}>Сохранить</button>
          </div>
        </div>
      </div>}
      <div className="conDataTable">
        <div className="datatable">
            {Object.entries(users).map(([login, obj]) => {
              if (obj.role !== 'superadmin')
              return (
                <div className="cell" key={login}>
                  <p>Логин: </p> <p><b>{login}</b></p>
                  <p>Пароль: </p> <p><b>{obj.pass}</b></p>
                  <p>Роль: </p><p><b>{
                    obj.role == 'admin' && 'Администратор' ||
                    obj.role == 'user' && 'Пользователь'
                  }</b></p>
                  <button onClick={() => $.post('/api/delete', {login})}>Удалить</button>
                </div>
              );
  
            })}
        </div>
      </div>
      </div>
    );
}
